'use client';

import { useState } from 'react';
import { MapComponent } from '@/components/map';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Switch } from '@/components/ui/switch';
import { Droplets, Car, PowerOff, Sparkles, LoaderCircle } from 'lucide-react';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { summarizeIncidents } from '@/ai/flows/summarize-incidents-flow';
import { incidents as allIncidents } from '@/components/map';
import { useToast } from '@/hooks/use-toast';

export default function MapPage() {
  const [showWaterLogging, setShowWaterLogging] = useState(true);
  const [showAccidents, setShowAccidents] = useState(false);
  const [showPowerCuts, setShowPowerCuts] = useState(false);
  const [summary, setSummary] = useState<string | null>(null);
  const [isSummarizing, setIsSummarizing] = useState(false);
  const { toast } = useToast();

  const getFilteredIncidents = () => {
    return allIncidents.filter(incident => {
      if (showWaterLogging && incident.category === 'Water Logging') return true;
      if (showAccidents && incident.category === 'Accident') return true;
      if (showPowerCuts && incident.category === 'PowerCut') return true;
      return false;
    });
  };

  const handleSummarize = async () => {
    const filteredIncidents = getFilteredIncidents();
    if (filteredIncidents.length === 0) {
      toast({
        variant: 'destructive',
        title: 'No Incidents Selected',
        description: 'Please select at least one incident type to summarize.',
      });
      return;
    }

    setIsSummarizing(true);
    setSummary(null);
    try {
      const result = await summarizeIncidents({ incidents: filteredIncidents });
      setSummary(result.summary);
    } catch (error) {
      console.error('Summarization failed', error);
      toast({
        variant: 'destructive',
        title: 'Summarization Failed',
        description: 'Could not generate a summary. Please try again.',
      });
    } finally {
      setIsSummarizing(false);
    }
  };


  return (
    <div className="bg-background min-h-screen">
      <main className="container mx-auto px-4 py-8 md:py-16">
        <header className="text-center mb-12">
          <h1 className="text-4xl sm:text-5xl font-bold font-headline text-primary">Incidents Map</h1>
          <p className="mt-4 text-lg text-muted-foreground">Visualize reported incidents on the map.</p>
          <div className="mt-6 max-w-2xl mx-auto">
            <div className="relative">
              <Input
                type="text"
                placeholder="Ask AI to summarize visible incidents..."
                className="w-full h-12 pr-28"
                disabled
              />
              <Button 
                className="absolute right-2 top-1/2 -translate-y-1/2 h-9"
                onClick={handleSummarize}
                disabled={isSummarizing}
              >
                {isSummarizing ? (
                  <>
                    <LoaderCircle className="mr-2 h-4 w-4 animate-spin" />
                    Summarizing...
                  </>
                ) : (
                  <>
                    <Sparkles className="mr-2 h-4 w-4" />
                    Get Summary
                  </>
                )}
              </Button>
            </div>
          </div>
        </header>
        
        {summary && (
          <Card className="mb-8 shadow-lg animate-in fade-in-50">
            <CardHeader>
              <CardTitle className="font-headline text-2xl flex items-center gap-2">
                <Sparkles className="w-6 h-6 text-primary" />
                AI Summary
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-muted-foreground leading-relaxed">{summary}</p>
            </CardContent>
          </Card>
        )}

        <div className="relative">
          <MapComponent 
            showWaterLogging={showWaterLogging} 
            showAccidents={showAccidents}
            showPowerCuts={showPowerCuts}
          />
          <Card className="absolute top-4 left-4 z-10 w-64 shadow-lg">
            <CardHeader>
              <CardTitle className="text-lg">Map Filters</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex items-center space-x-3">
                <Droplets className="w-5 h-5 text-primary" />
                <Label htmlFor="water-logging-switch" className="flex-grow">Show Water Logging</Label>
                <Switch
                  id="water-logging-switch"
                  checked={showWaterLogging}
                  onCheckedChange={setShowWaterLogging}
                />
              </div>
              <div className="flex items-center space-x-3">
                <Car className="w-5 h-5 text-primary" />
                <Label htmlFor="accidents-switch" className="flex-grow">Show Accidents</Label>
                <Switch
                  id="accidents-switch"
                  checked={showAccidents}
                  onCheckedChange={setShowAccidents}
                />
              </div>
              <div className="flex items-center space-x-3">
                <PowerOff className="w-5 h-5 text-primary" />
                <Label htmlFor="power-cuts-switch" className="flex-grow">Show Power Cuts</Label>
                <Switch
                  id="power-cuts-switch"
                  checked={showPowerCuts}
                  onCheckedChange={setShowPowerCuts}
                />
              </div>
            </CardContent>
          </Card>
        </div>
      </main>
    </div>
  );
}
